.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

nav {
  display: flex;
  flex-direction: row;
  background: rgb(240,240,240, 0.5);
  box-shadow: rgb(100 100 111 / 20%) 0px 7px 29px 0px;
  padding: 5px 15px;
}

.content {
  background: rgba(204, 204, 204, 0.5);
  min-height: 80vh;
  margin: 45px;
  padding: 50px;
  display: flex;
  flex-direction: column;
  justify-content: start;
}

.font {
  color: brown
}

.bdg {
  display: inline;
  font-weight: bold;
  border-radius: 20px;
  padding: 8px;
  width: 130px;
  margin: 10px 25px 10px 0px;
  text-align: center;
  cursor: pointer;
  transition: all .3s ease;
}

.bdg-off {
  background: #a5a5a5;
}

.bdg-on {
  background-color: brown;
  color: white;
}

.bdg:hover {
  transform: scale(1.2);
  background-color: brown;
  color: white;
}
.text-center {
  text-align: center;
}

.mt-30 {
  margin-top: 30px
}